<template>
  <div>
    <div>
      <el-card :body-style="{ padding: '10px' }">
        <div style="text-align: right">
          <el-dropdown>
            <span class="el-dropdown-link">
              <el-avatar :size="18" :src="user"></el-avatar>
              ???（gpt-3.5-turbo）
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="handle2Theme">
                <i class="el-icon-sunrise-1"></i>
                主题
              </el-dropdown-item>
              <el-dropdown-item @click.native="handleLogOut" style="color: red">
                <i class="el-icon-switch-button"></i>
                退出
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-card>
    </div>
    <div v-if="chatDataList && chatDataList.length > 0">
      <div ref="chatRef" :style="{ height: height + 'px', overflowY: 'auto' }">
        <div v-for="(item, index) in chatDataList" :key="index">
          <div v-if="index % 2 === 0" class="my_chat">
            <el-avatar :size="32" :src="user"></el-avatar>
            <span style="margin-left: 20px; width: 50%">
              {{ item.content }}
            </span>
          </div>
          <div v-else class="ai_chat">
            <el-avatar :size="32" :src="chatGpt"></el-avatar>
            <span style="margin-left: 20px; width: 50%">
              <v-md-preview :text="item.content"></v-md-preview>
            </span>
          </div>
        </div>
      </div>
    </div>
    <div style="margin-top: 50px" v-else>
      <el-empty description="暂无对话"></el-empty>
    </div>
    <div class="send-card">
      <el-card :body-style="{ padding: '0px' }" v-loading="loading">
        <el-input
          class="chat-input"
          autosize
          maxlength="36"
          v-model="content"
          placeholder="请输入你的问题"
        >
          <i
            class="el-icon-s-promotion el-input__icon"
            slot="suffix"
            @click="handleSend"
          >
          </i>
        </el-input>
      </el-card>
    </div>
  </div>
</template>

<script>
import chatGpt from "@/assets/images/chatgpt.png";
import user from "@/assets/images/user.png";

export default {
  components: {},
  name: "chatIndex",
  data() {
    return {
      loading: false,
      height: window.innerHeight - 180,
      theme: 1,
      content: "",
      contentPre: "",
      chatDataList: [],
      chatGpt: chatGpt,
      user: user,
      url: "https://api.chatanywhere.com.cn/v1/chat/completions",
      apiKey: "sk-Cg8AqAWoQxhqetjTXfdzOeDm5yKu3zUPdmAg9bweMkxIDskj",
    };
  },
  created() {
    this.chatDataList = [];
  },
  mounted() {
    document.addEventListener("keyup", this.enterKeyDown);
  },
  destroyed() {
    document.removeEventListener("keyup", this.enterKeyDown);
  },
  methods: {
    enterKeyDown(event) {
      if (event.keyCode === 13) {
        this.handleSend();
      }
      if (event.keyCode === 38) {
        this.content = this.contentPre;
      }
    },
    reachBottom() {
      const _chatRef = this.$refs.chatRef;
      if (_chatRef) {
        setTimeout(() => {
          _chatRef.scrollTop = _chatRef.scrollHeight;
        }, 0);
      }
    },
    handle2Theme() {
      console.log("123");
    },
    handleLogOut() {
      this.$router.push({ path: "/" });
    },
    async handleSend() {
      this.loading = true;
      if (!this.content) {
        this.$message({
          message: "消息不能为空",
          type: "warning",
        });
        this.loading = false;
        return;
      }
      // 填充我的信息
      const _my = {
        role: "user",
        content: this.content,
      };
      this.chatDataList.push(_my);
      this.reachBottom();
      // 请求ai
      const _params = {
        model: "gpt-3.5-turbo",
        messages: this.chatDataList,
        stream: true, // 流式
      };
      const self = this;
      const res = await fetch(this.url, {
        method: "POST",
        headers: {
          Authorization: "Bearer " + this.apiKey,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(_params),
      });
      const reader = res.body.pipeThrough(new TextDecoderStream()).getReader();
      let flag = true;
      do {
        let { value, done } = await reader.read();
        if (done) {
          flag = false;
        }
        if (!value) {
          continue;
        }
        value = value?.replace("undefined", "");
        const _contentList = value.split("data:");
        if (_contentList && _contentList.length > 0) {
          _contentList.forEach((item) => {
            if (item && !item.includes("[DONE]")) {
              let _content = "";
              if (item === "\n\n" || item === "\n") {
                _content = "";
              } else {
                const _contentObj = JSON.parse(item.trim());
                if (_contentObj.choices[0].delta.content) {
                  _content = _contentObj.choices[0].delta.content;
                }
              }
              const _lastContent =
                self.chatDataList[self.chatDataList.length - 1];
              if (_lastContent.role === "assistant") {
                self.chatDataList[self.chatDataList.length - 1].content =
                  _lastContent.content + _content;
              } else {
                const _ai = {
                  role: "assistant",
                  content: _content,
                };
                self.chatDataList.push(_ai);
              }
              self.reachBottom();
            }
          });
        }
      } while (flag);
      self.contentPre = self.content;
      self.content = "";
      self.loading = false;
    },
  },
};
</script>

